







































































































import { Vue, Component } from 'vue-property-decorator';

import { EFormType, EFormMode } from '@/../enums';
import { ServiceRegex, ServiceToast, ServiceResponseFilter, ServiceServerConfig, ServiceFormConfig } from '@/../helpers';
import { IComponent } from '@/../models';

import { ModelsSMTP as Model } from './models';

import Loading from '@/../components/loading/loading.vue';
import Forms from '@/../components/form/forms.vue';
import Card from '@/../components/card/card.vue';
import Modal from '@/../components/modal/modal.vue';

@Component({
    components: { Forms, Card, Modal },
})
export default class VuePageClass extends Vue {
    //#region Variables & Computed
    private formItem: Model.IFormItem = {
        isClearState: false,
        isClearTestState: false,
        isTest: false,
        modalShow: false,
    };

    private formDataOriginal: Model.IFormData = {
        host: undefined,
        port: 25,
        email: undefined,
        password: undefined,
        testEmail: undefined,
        ssl: false,
    };
    private formData: Model.IFormData = { ...this.formDataOriginal };

    private formConfig: Model.IFormConfigs = {
        host: JSON.parse(JSON.stringify(ServiceFormConfig.getFormConfig())),
        port: JSON.parse(JSON.stringify(ServiceFormConfig.getFormConfig())),
        email: JSON.parse(JSON.stringify(ServiceFormConfig.getFormConfig())),
        password: JSON.parse(JSON.stringify(ServiceFormConfig.getFormConfig())),
        ssl: JSON.parse(JSON.stringify(ServiceFormConfig.getFormConfig())),
        testEmail: JSON.parse(JSON.stringify(ServiceFormConfig.getFormConfig())),
    };
    private formConfigs: IComponent.IFormConfig[] = [];

    private formTestConfigs: IComponent.IFormConfig[] = [];

    private formButtonDisabled: Model.IFormButtonDisabled = {
        host: true,
        port: true,
        email: true,
        password: true,
        ssl: true,
        testEmail: true,
    };
    private get formSaveDisable(): boolean {
        if (!this.formItem.modalShow) {
            return this.formItem.isTest ? false : true;
        } else {
            if (this.formButtonDisabled.testEmail) return true;
        }

        return false;
    }

    private get formTestDisable(): boolean {
        this.formButtonDisabled.testEmail = false;
        return Object.values(this.formButtonDisabled).some((x) => x === true);
    }

    private eFormMode = EFormMode;
    private loading = new Loading();
    //#endregion

    //#region Vue Life
    private async mounted(): Promise<void> {
        this.initFormConfig();
        await this.initFormData();
    }
    //#endregion

    //#region Function

    //#region Init Data
    private initFormConfig(): void {
        this.formConfigs = [];
        this.formTestConfigs = [];

        // host
        this.formConfig.host.id = 'host';
        this.formConfig.host.label = this.$i18n.Settings_SMTP_Host;
        this.formConfig.host.placeholder = this.$i18n.Settings_SMTP_Host;
        this.formConfig.host.formType = EFormType.text;
        this.formConfig.host.required = true;
        this.formConfig.host.regex = ServiceRegex.hostname();
        this.formConfig.host.regexErrorTip = this.$i18n.Settings_SMTP_Host;

        // port
        this.formConfig.port.id = 'port';
        this.formConfig.port.label = this.$i18n.Settings_SMTP_Port;
        this.formConfig.port.placeholder = this.$i18n.Placeholder_Port;
        this.formConfig.port.formType = EFormType.number;
        this.formConfig.port.required = true;
        this.formConfig.port.regex = ServiceRegex.port();
        this.formConfig.port.regexErrorTip = this.$i18n.Error_Format_Port;
        this.formConfig.port.max = 65535;
        this.formConfig.port.min = 1;

        // email
        this.formConfig.email.id = 'email';
        this.formConfig.email.label = this.$i18n.Settings_SMTP_Email;
        this.formConfig.email.placeholder = this.$i18n.Placeholder_Email;
        this.formConfig.email.formType = EFormType.text;
        this.formConfig.email.required = true;
        this.formConfig.email.regex = ServiceRegex.email();
        this.formConfig.email.regexErrorTip = this.$i18n.Error_Format_Email;

        // password
        this.formConfig.password.id = 'password';
        this.formConfig.password.label = this.$i18n.Common_Password;
        this.formConfig.password.placeholder = this.$i18n.Common_Password;
        this.formConfig.password.formType = EFormType.password;
        this.formConfig.password.required = true;

        // testEmail
        this.formConfig.testEmail.id = 'testEmail';
        this.formConfig.testEmail.label = this.$i18n.Common_Email;
        this.formConfig.testEmail.placeholder = this.$i18n.Placeholder_Email;
        this.formConfig.testEmail.formType = EFormType.text;
        this.formConfig.testEmail.required = true;
        this.formConfig.testEmail.regex = ServiceRegex.email();
        this.formConfig.testEmail.regexErrorTip = this.$i18n.Error_Format_Email;

        // ssl
        this.formConfig.ssl.id = 'ssl';
        this.formConfig.ssl.label = this.$i18n.Settings_SMTP_SSL;
        this.formConfig.ssl.placeholder = this.$i18n.Settings_SMTP_SelectSSLStatus;
        this.formConfig.ssl.formType = EFormType.switch;
        this.formConfig.ssl.required = true;

        this.formConfigs = [this.formConfig.host, this.formConfig.port, this.formConfig.email, this.formConfig.password, this.formConfig.ssl];

        this.formTestConfigs = [this.formConfig.testEmail];
    }

    private async initFormData(): Promise<void> {
        this.loading.show();
        let data = JSON.parse(JSON.stringify(SMTP_SETTING));

        if (data) setTimeout(() => this.formDataSet(data), 100);

        this.loading.hide();
    }
    //#endregion

    //#region Form Set
    private formDataClear(): void {
        this.formData = JSON.parse(JSON.stringify({ ...this.formDataOriginal }));
    }

    private formConfigValues(value: any, disabledButton: boolean, id: string): void {
        if (this.formData.hasOwnProperty(id)) this.formData[id] = value;
        if (this.formButtonDisabled.hasOwnProperty(id)) this.formButtonDisabled[id] = value;

        this.formButtonDisabled.testEmail = true;
        this.formItem.isTest = false;
    }

    private formDataSet(data: Model.ISettingSMTPParam): void {
        this.formData.host = data.host ? data.host : '';
        this.formData.port = data.port ? data.port : null;
        this.formData.email = data.email ? data.email : '';
        this.formData.password = data.password ? data.password : '';
        this.formData.ssl = data.ssl;
    }
    //#endregion

    private async dataSave(): Promise<void> {
        let data: Model.IFormData = this.formData;

        let param: Model.ISettingSMTPParam = {
            host: data.host,
            port: data.port,
            email: data.email,
            password: data.password,
            ssl: data.ssl,
        };

        this.loading.show();

        SMTP_SETTING = {
            host: data.host,
            port: data.port,
            email: data.email,
            password: data.password,
            ssl: data.ssl,
        };

        this.loading.hide();
        ServiceToast.success(this.$i18n.Dialog_SaveSuccess);
        this.formItem.isClearState = false;
    }

    private async dataTest(): Promise<void> {
        let data: Model.IFormData = this.formData;

        const param: {
            config: Model.IConfig;
            email: string;
        } = {
            config: {
                host: data.host,
                port: data.port,
                email: data.email,
                password: data.password,
                ssl: data.ssl,
            },
            email: this.formData.testEmail,
        };

        this.loading.show();

        this.loading.hide();
        this.clickCloseModal();
        ServiceToast.success(this.$i18n.Common_Status_TestSuccess);
        this.formItem.isTest = true;
    }

    //#region Modal
    private clickShowModal() {
        this.formData.testEmail = '';
        this.formItem.isClearTestState = true;
        this.formItem.modalShow = !this.formItem.modalShow;
    }

    private clickCloseModal() {
        this.formItem.isClearTestState = false;
        this.formItem.modalShow = false;
    }
    //#endregion

    //#endregion
}

//#region Fake Data
let SMTP_SETTING = {
    host: 'mail.advantech.com.tw',
    port: 25,
    email: 'services@advantech.com.tw',
    password: 'Az123456',
    ssl: false,
};
//#endregion
